import React, { Component } from 'react'
import {
    Button,
    Header,
    Icon,
    Image,
} from 'semantic-ui-react'

var count = 1;
var a = []

class ChooseHeaderImg extends Component {
    constructor (props) {
        super(props)

        if (typeof document !== 'undefined'){
            while(localStorage.getItem(count)){
                a.push(localStorage.getItem(count)); 
                count++ 
            }
        }

        this.state = {
            defaultUrl: a.length>0?a[0]:'',
            imgList : a,
        }
    }

    chooseImg = (url) => {
        this.setState({
            defaultUrl: url,
        })
    }


    render() {
        const ImageList = this.state.imgList.map((url) =>
            <Image 
                verticalAlign='top'
                onClick={()=>{this.chooseImg(url)}} 
                src={url} 
                style={{cursor: 'pointer'}}/>
        );
        return(
            <div> 
            {this.state.defaultUrl !== ''?
                <div>
                    <Header as='h4'>Preview Image</Header>
                    <div style={{position: 'relative',
                                textAlign: 'center',
                                color: 'white'}}>
                        <Image size="medium"
                            bordered 
                            style={{maxWidth:'100%',maxHeight:'100%'}} 
                            wrapped
                            src={this.state.defaultUrl} />
                    </div>
                    <Button icon 
                        onClick={()=>{this.setState({defaultUrl:''})}} 
                        style={{
                            background: 'rgba(0,0,0,0.3)',
                            color:'white',
                            padding:4,
                            position: 'absolute',
                            top: 115,
                            left:22,
                            }}>
                        <Icon name='camera' />
                    </Button>
                </div>:
                <div>
                    {this.state.imgList.length == 0 ?
                        <div>
                            <p>No Preview Image</p>
                            <Image src='https://react.semantic-ui.com/images/wireframe/image.png' size='small' />
                        </div>
                    :
                    <div>
                        <p>Select Preview Image</p>
                        <Image.Group size='tiny'>
                            {ImageList}
                        </Image.Group>
                    </div>
                    }

                </div>
            }
                 
            </div>            
        )
    }
}

export default ChooseHeaderImg

